.options-bar {
    display: flex; /* Use flexbox to arrange buttons horizontally */
    justify-content: center; /* Center the buttons */
    margin: 20px 0; /* Space above and below the options bar */
  }
  
  .options-button {
    margin: 0 10px; /* Space between buttons */
    padding: 10px 20px; /* Padding for buttons */
    cursor: pointer; /* Pointer cursor */
    border: none; /* Remove border */
    border-radius: 5px; /* Rounded corners */
    background-color: #4CAF50; /* Green background */
    color: white; /* White text */
    transition: background-color 0.3s ease; /* Smooth background color transition */
  }
  
  .options-button.active {
    background-color: #3c1eff; 
    color: black;
  }
  
  .options-button:not(.active) {
    background-color: #ccc; /* Grey out the other buttons */
    color: white; /* Change text color to a lighter shade */
  }
  
  .options-button:hover {
    background-color: #777777; /* Darker green on hover if not active */
  }
  