.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.main-container {
  height: 100vh;
}

.button-container {
  display: grid;
  grid-template-columns: repeat(6, 1fr); /* 6 buttons per row */
  gap: 10px; /* Space between buttons */
  justify-content: center; /* Centers the buttons horizontally */
  margin-top: 20px; /* Optional: Space above the buttons */
  max-width: 600px; /* Optional: Limit the width to ensure the buttons wrap */
  margin-left: auto;
  margin-right: auto;
}

.quiz-controls {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  margin-top: 18px;
  justify-content: center;
}

.quiz-controls-button {
  padding: 10px 20px;
  font-size: 16px;
  margin: 10px;
  cursor: pointer;
  justify-content: center;
  background-color: #bebebe; 
  color: black;
  border: 1px line black;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.end-quiz-button {
  background-color: #ff6347; 
  padding: 10px 20px;
  font-size: 16px;
  margin: 10px;
  cursor: pointer;
  justify-content: center;
  border: 1px line black;
  border-radius: 5px;

}

.start-quiz-button {
  background-color: #474aff; 
  padding: 10px 20px;
  font-size: 16px;
  margin: 10px;
  cursor: pointer;
  justify-content: center;
  border: 1px line black;
  border-radius: 5px;
  
}


button {
  padding: 10px 20px;
  margin: 10px;
  font-size: 16px;
  cursor: pointer;
}

h2 {
  margin: 20px 0;
}

.selected {
  border: 3px solid black; /* Border color when selected */
  animation: borderAnimation 0.3s ease;
}

.highlighted-type {
  color: #ff6347; /* You can change this to any color you want */
  font-weight: bold;
  text-decoration: underline;
}


@keyframes borderAnimation {
  0% {
    border-color: transparent;
  }
  50% {
    border-color: black;
  }
  100% {
    border-color: black;
  }
}

/* Style for type buttons to make them circular */
.type-button {
  width: 60px; /* Adjust size as needed */
  height: 60px; /* Adjust size as needed */
  border-radius: 50%; /* Makes the button circular */
  overflow: hidden; /* Hides overflow to keep images within bounds */
  cursor: pointer; /* Changes cursor to pointer on hover */
  position: relative; /* For positioning child elements */
  border: 3px solid transparent; /* Default border */
  transition: border-color 0.3s ease; /* Smooth border color transition */
}

.type-button img {
  width: 100%; /* Ensures image fits within button */
  height: 100%; /* Ensures image fits within button */
  object-fit: cover; /* Maintains aspect ratio */
}

.correct {
  background-color: lightgreen; /* Feedback color for correct answers */
}

.incorrect {
  background-color: lightcoral; /* Feedback color for incorrect answers */
}

.feedback-message {
  margin-top: 10px; /* Space above feedback messages */
  font-weight: bold; /* Makes feedback bold */
  font-size: 24px;
}


.chart-caption {
  height: 100%;
}

.final-score {
  font-size: 24px;
  font-weight: bold;
}


