.type-button {
    width: 60px; /* Adjust size as needed */
    height: 60px; /* Ensure height equals width for a circle */
    margin: 5px; /* Space between buttons */
    padding: 0; /* Remove padding to eliminate gaps */
    border: none; /* Remove default border */
    border-radius: 50%; /* Make the button circular */
    cursor: pointer;
    outline: none;
    background-color: transparent; /* No background color */
    transition: transform 0.3s ease; /* Smooth transition for scaling */
    position: relative; /* Position relative to adjust inner elements */
  }
  
  .type-button img {
    width: 100%;
    height: 100%;
    border-radius: 50%; /* Make the image circular */
    object-fit: cover; /* Ensure the image covers the button area */
    display: block; /* Prevent gaps caused by inline elements */
  }
  
  .type-button.selected {
    box-shadow: 0 0 0 4px black; /* Black border effect with shadow */
    transform: scale(1.1); /* Slightly scale the button when selected */
  }
  
